    }
  },
  components: {
    headerSearch
  },
  methods: {
    updateView () {
      var arr = Array.prototype.slice.call(this.$refs.brandList.childNodes)
      arr.forEach((item, index) => {
        this.brandList[index].height = parseInt(window.getComputedStyle(item).height.replace('px', ''))
        this.brandList[index].top = item.offsetTop
      })
    },

    go (top) {
      this.goScroll(top)
    },
    
    goScroll (top) {
      var requestAnimationFrame
      if (window.requestAnimationFrame) {
        requestAnimationFrame = window.requestAnimationFrame
      } else {
        requestAnimationFrame = setTimeout((fn) => {
          fn()
        }, 17)
      }
      // 当前时间
      var t = 0
      // 初始值
      var b = window.scrollY
      // 变化量
      var c = top - b
      // 持续时间
      var d = 20
      var step = () => {
        var v = this.run(t, b, c, d)
        // console.log(v)
        window.scrollTo(0, v)
        t += 1
        if (t <= d) {
          requestAnimationFrame(step)
        }
      }
      step()
    },
    // 运动
    run (t, b, c, d) {
      return c * (t /= d) * t + b
    },

  },
  mounted () {
    window.onscroll = () => {
      var y = window.scrollY
      if (y > 260) {
        this.$refs.menu.style.position = 'fixed'
      } else {
        this.$refs.menu.style.position = 'absolute'
      }
      this.updateView()
    }
  },
  destroyed () {
    window.onscroll = null
  }
}
