<template>
  <main class="type">
    <header-search></header-search>
    <section class="type-list">
      <van-tabs swipeable v-model="index">
        <van-tab v-for="(item) in tabList" :title="item.name" :key="item.id">
          <van-pull-refresh v-model="pull" @refresh="refresh(item)"> 
            <section class="show-list">
              <a class="show-item" v-for="child in item.list" :key="child.id" @click="go(child.id)">
                <img :src="`${baseUrl}common/${child.img}`" alt="">
                <p>{{ child.name }}</p>
              </a>
            </section>
          </van-pull-refresh>
        </van-tab>   
      </van-tabs>
    </section>
    <div class="foot-menu">
      <router-link :to="{path: `${path}/`}">{{ options.foot.btn1 }}</router-link>
      <router-link :to="{path: `${path}/type`}">{{ options.foot.btn2 }}</router-link>
    </div>
  </main>
</template>

<script>
import headerSearch from  '../components/header-search'
import { Toast } from 'vant'
export default {
  data () {
    return {
      index: 0,
      baseUrl: process.env.BASE_URL,
      pull: false,
      tabList: [
        {
          id: 1,
          name: '乳制品',
          pull: false,
          list: []
        },
        {
          id: 2,
          name: '果蔬',
          pull: false,
          list: []
        },
        {
          id: 3,
          name: '蜂蜜',
          pull: false,
          list: []

        },
        {
          id: 4,
          name: '肉制品',
          pull: false,
          list: []
        },
        {
          id: 5,
          name: '服装',
          pull: false,
          list: []
        },
        {
          id: 6,
          name: '电器',
          pull: false,
          list: []
        }
      ],
      default: [
        {
          id: 1,
          name: '安佳调制乳粉安佳高钙全脂中老年成人青少年学生奶粉',
          img: 'item3.png'
        },
        {
          id: 2,
          name: '新西兰制造 酸奶粉香草味',
          img: 'item1.png'
        },
        {
          id: 3,
          name: '新西兰制造 酸奶粉草莓味',
          img: 'item2.png'
        },
        {
          id: 4,
          name: '新西兰制造 酸奶草莓味',
          img: 'item1.png'
        }
      ]
    }
  },
  computed: {
    options () {
      return this.$t('type')
    },
    path () {
      return this.$store.getters.getPath
    }
  },
  watch: {
    index (val) {
      let arr = this.tabList[val].list
      if (arr.length === 0) {
        this.getList()
      }
      this.pull = false
    }
  },
  components: {
    headerSearch
  },
  methods: {
    refresh (item) {
      this.$nextTick(() => {
        item.list = []
        this.getList()
      })
    },

    getList () {
      let item = this.tabList[this.index]
      if (item.list.length === 0) {
        this.showToast()
        setTimeout(() => {
          item.list = this.default
          this.clearToast()
          this.pull = false
        }, 1000)
      }
    },


    showToast () {
      Toast.loading({
        duration: 0,
        mask: true,
        message: this.options.loading
      })
    },

    clearToast () {
      Toast.clear()
    },

    go (id) {
      this.$router.push(`${this.path}/detail?id=${id}`)
    }
  },
  mounted () {
    this.getList()
  }
}
</script>


<style lang="scss" scoped>
.tab {
  height: 100vh;
}

.show-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  align-content: flex-start;
  min-height: 100vh;
  padding: 0 5%;
}

.show-item {
  width: 45%;

  img {
    width: 90%;
  }

  p {
    font-size: 12px;
  }
}
</style>
