<template>
  <header>
    <router-link :to="{path: `${path}/`}">
      <img :src="`${baseUrl}${lang}/logo-small.png`" alt="logo" class="logo">
    </router-link>
    <label class="search">
      <van-icon name="search" />
      <input type="text" :placeholder="options.search.placeholder" @keyup.enter="search"  v-model="keywords">
    </label>
    <p class="language" @click="() => this.showList = true">
      <span>{{ this.langName }}</span>
      <i class="iconfont icon-arrowdown-copy"></i>
    </p>
    <van-popup v-model="showList" position="bottom">
      <section class="lang-list">
        <a v-for="item in langList" :key="item.name" @click="changeLanguage(item.lang)">
          {{ item.name }}
        </a>
      </section>
    </van-popup>
  </header>
</template>

<script>
export default {
  data () {
    return {
      baseUrl: process.env.BASE_URL,
      showList: false,
      keywords: ''
    }
  },
  computed: {
    lang () {
      return this.$store.getters.getLanguage
    },
    langList () {
      return this.$store.getters.getLanList
    },
    path () {
      return this.$store.getters.getPath
    },
    options () {
      return this.$t('home')
    },
    langName () {
      let arr = this.langList
      let name = '中文'
      arr.forEach(item => {
        if (item.lang === this.lang) {
          name = item.name
        }
      })
      return name
    }
  },
  methods: {
    search () {
      console.log('search')
      this.$emit('search', this.keywords)
    },

    changeLanguage (data) {
      this.$store.dispatch ({
        type: 'setLanguage',
        language: data
      })
      this.showList = false
    }
  }
}
</script>


