<template>
  <main class="product">
    <img :src="`${baseUrl}common/p1.png`" alt="" class="product-img">
    <header-search></header-search>
    <van-pull-refresh v-model="pull" @refresh="refresh()"> 
      <section class="show-list">
        <a class="show-item" v-for="child in list" :key="child.id" @click="go(child.id)">
          <img :src="`${baseUrl}common/${child.img}`" alt="">
          <p>{{ child.name }}</p>
        </a>
      </section>
    </van-pull-refresh>
    <div class="foot-menu">
      <router-link :to="{path: `${path}/`}">{{ options.foot.btn1 }}</router-link>
      <router-link :to="{path: `${path}/introduce?id=${id}`}">{{ options.foot.btn2 }}</router-link>
      <router-link :to="{path: `${path}/buy?id=${id}`}">{{ options.foot.btn3 }}</router-link>
    </div>
  </main>
</template>

<script>
import headerSearch from  '../components/header-search'
import { Toast } from 'vant'
export default {
  data () {
    return {
      baseUrl: process.env.BASE_URL,
      id: 1,
      pull: false,
      page: 1,
      list: [],
      default: [
        {
          id: 1,
          name: '安佳调制乳粉安佳高钙全脂中老年成人青少年学生奶粉',
          img: 'item3.png'
        },
        {
          id: 2,
          name: '新西兰制造 酸奶粉香草味',
          img: 'item1.png'
        },
        {
          id: 3,
          name: '新西兰制造 酸奶粉草莓味',
          img: 'item2.png'
        },
        {
          id: 4,
          name: '新西兰制造 酸奶草莓味',
          img: 'item1.png'
        }
      ]
    }
  },
  computed: {
    options () {
      return this.$t('products')
    },
    path () {
      return this.$store.getters.getPath
    }
  },
  components: {
    headerSearch
  },
  methods: {
     getList () {
      let arr = this.list
      if (arr.length === 0) {
        this.showToast()
        setTimeout(() => {
          this.list = this.default
          this.clearToast()
          this.pull = false
        }, 1000)
      }
    },

     showToast () {
      Toast.loading({
        duration: 0,
        mask: true,
        message: this.options.loading
      })
    },

    clearToast () {
      Toast.clear()
    },

    go (id) {
      this.$router.push(`${this.path}/detail?id=${id}`)
    },
    
    refresh () {
      this.page = 1
      this.list = []
      this.getList()
    }
  },
  mounted () {
    let id = ''
    if (this.$route.query.id) {
      id = this.$route.query.id
    }
    this.id = id
    this.getList()
  }
}
</script>


