  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  align-content: flex-start;
  min-height: 100vh;
  padding: 0 5%;
}

.show-item {
  width: 45%;

  img {
    width: 90%;
  }

  p {
    font-size: 12px;
  }
}
</style>

